import React, { useEffect, useRef, useState } from 'react';
import { DataFrame, Field, PanelProps } from '@grafana/data';
import { PanelType, SimpleOptions, TopicProtocol } from 'types';
import { css, cx } from 'emotion';
import { Spinner, stylesFactory } from '@grafana/ui';
import * as mqtt from 'mqtt';
import { getDataSourceSrv } from '@grafana/runtime';
import SliderPanel from './panels/Slider';
import SwitchPanel from './panels/Switch';
import MultiSwitchPanel from './panels/MultiSwitch';
import SingleStatPanel from './panels/SingleStat';

interface Props extends PanelProps<SimpleOptions> {}

export const BasePanel: React.FC<Props> = props => {
  let _client: any = useRef(null);

  const { options, data, width, height } = props;
  const [value, setValue] = useState<any>(null);
  const [isRunning, setIsRunning] = useState(false);
  const [topicProtocols, setTopicProtocols] = useState<TopicProtocol[]>([]);
  const [dataSources, setDataSources] = useState<any>([]);
  const [isConnected, setIsConnected] = useState(false);

  useEffect(() => {
    const series: DataFrame[] = data?.series;
    const fields: Field[] = (series && series.length && series[0]?.fields) || [];
    const index = fields.map((field: Field) => field.name).indexOf('value');
    if (index !== -1) {
      const values = fields[index].values;
      if (values && values.length) {
        setValue(values.get(values.length - 1));
      }
    }
    setIsRunning(false);
  }, [data]);

  useEffect(() => {
    const targets: any[] = data?.request?.targets || [];
    const mqttValidConfig: any[] = targets.filter(target => !!target.topic);
    const topicsProtocols$: TopicProtocol[] = mqttValidConfig.map(target => ({
      topic: target.topic,
      messaging: target.messaging,
    }));
    setTopicProtocols(topicsProtocols$);
    if (mqttValidConfig.length) {
      const dataSources$: any[] = mqttValidConfig.map(x => x.datasource);
      if (JSON.stringify(dataSources) !== JSON.stringify(dataSources$)) {
        setDataSources(dataSources$);
      }
    }
  }, [data]);

  useEffect(() => {
    if (_client.current) {
      _client.current.end();
    }
    for (const dataSource of dataSources) {
      let match = false;
      getDataSourceSrv()
        .get(dataSource)
        .then(res => {
          if (res.meta.id === 'nubeio-mqtt-data-source') {
            // @ts-ignore
            const { mqttOptions } = res;
            _client.current = mqtt.connect(mqttOptions);
            _client.current.on('connect', () => {
              setIsConnected(true);
            });
            match = true;
          }
        });
      if (match) {
        break;
      }
    }
  }, [dataSources]);

  useEffect(() => {
    return () => {
      if (_client.current) {
        _client.current.end();
      }
    };
  }, []);

  const { panelType, multiSwitchTab } = options;
  const styles = getStyles();
  return (
    <div
      className={cx(
        styles.wrapper,
        css`
          width: ${width}px;
          height: ${height}px;
        `
      )}
    >
      {panelType === PanelType.Slider && (
        <SliderPanel
          _client={_client}
          topicProtocols={topicProtocols}
          options={options}
          isRunning={isRunning}
          setIsRunning={setIsRunning}
          dataValue={value}
        />
      )}
      {panelType === PanelType.SingleStat && (
        <SingleStatPanel options={options} isRunning={isRunning} setIsRunning={setIsRunning} dataValue={value} />
      )}
      {panelType === PanelType.Switch && (
        <SwitchPanel
          _client={_client}
          options={options}
          topicProtocols={topicProtocols}
          isRunning={isRunning}
          setIsRunning={setIsRunning}
          dataValue={value}
        />
      )}
      {panelType === PanelType.MultiSwitch && (
        <MultiSwitchPanel
          _client={_client}
          options={options}
          topicProtocols={topicProtocols}
          multiSwitchTab={multiSwitchTab}
          isRunning={isRunning}
          setIsRunning={setIsRunning}
          dataValue={value}
        />
      )}
      {isRunning && (
        <div className={styles.spinner}>
          <Spinner size={12} />
        </div>
      )}
      {(isConnected && <div className={styles.greenDot} />) || <div className={styles.redDot} />}
    </div>
  );
};

const dot = css`
  @extend dot;
  position: absolute;
  top: -32px;
  left: -2px;
  margin: 2px;
  height: 12px;
  width: 12px;
  border-radius: 50%;
  display: inline-block;
`;

const getStyles = stylesFactory(() => {
  return {
    wrapper: css`
      position: relative;
    `,
    spinner: css`
      position: absolute;
      top: -32px;
      right: -2px;
    `,
    redDot: css`
      ${dot};
      background: radial-gradient(
          circle farthest-corner at 33% 33%,
          rgba(242, 0, 0, 0.85) 0%,
          rgba(150, 20, 20, 0.85) 80%
        ),
        radial-gradient(circle farthest-corner at 45% 45%, rgba(0, 0, 0, 0) 50%, #000000 80%);
    `,
    greenDot: css`
      ${dot};
      background: radial-gradient(
          circle farthest-corner at 33% 33%,
          rgba(77, 197, 21, 0.6) 0%,
          rgba(96, 183, 56, 1) 80%
        ),
        radial-gradient(circle farthest-corner at 45% 45%, rgba(50, 99, 28, 0) 50%, #418522 80%);
    `,
  };
});
