export enum PanelType {
  MultiSwitch = 'Multi Switch',
  Slider = 'Slider',
  Switch = 'Switch',
  SingleStat = 'Single Stat',
}

export enum MultiSwitchType {
  BUTTON = 'Button',
  DROPDOWN = 'Drop Down',
}

export interface MultiSwitchTabDataType {
  [id: number]: {
    name: string;
    value: any;
  };
}

export interface MultiSwitchTabType {
  type: MultiSwitchType;
  data: MultiSwitchTabDataType;
}

export interface SimpleOptions extends SliderOptions, MultiSwitchOptions, SingleStatOptions {
  panelType: PanelType;
}

export interface SliderOptions {
  min?: number;
  max?: number;
}

export interface MultiSwitchOptions {
  multiSwitchTab?: MultiSwitchTabType;
}

export interface SingleStatOptions {
  unit?: string;
  fontSize?: number;
  fontColor?: string;
  precision?: number;
  image?: string;
  comparator?: string;
  trueState?: string;
  showValue?: boolean;
  trueStatus?: string;
  falseStatus?: string;
  trueFontColor?: string;
}

export enum Messaging {
  DEFAULT = 'DEFAULT',
  REQ_RES = 'REQ_RES',
}

export interface TopicProtocol {
  topic: string;
  messaging: Messaging;
}

export enum Image {
  NoImage = 'No Image',
  Light = 'Light',
  Fan = 'Fan',
  Cooling = 'Cooling',
  Heating = 'Heating',
  Alert = 'Alert',
}

export enum Comparator {
  Equal = 'Equal',
  GreaterThan = 'Greater Than',
  GreaterThanOrEqual = 'Greater Than Or Equal',
  LessThan = 'Less Than',
  LessThanOrEqual = 'Less Than Or Equal',
}
