import React, { useState } from 'react';
import { StandardEditorProps } from '@grafana/data';
import ColorSelector from '../components/RenderProps/ColorSelector';
import { createStyles, TextField, Theme } from '@material-ui/core';
import makeStyles from '@material-ui/core/styles/makeStyles';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    body: {
      width: '100%',
    },
    input: {
      marginBottom: '20px',
    },
    textField: {
      marginRight: theme.spacing(2),
      minWidth: 170,
    },
    colorPreview: {
      marginRight: '8px',
      padding: '4px',
      background: '#fff',
      borderRadius: '4px',
      display: 'inline-block',
      border: '1px solid',
      cursor: 'pointer',
      '& div': {
        width: '32px',
        height: '28px',
        borderRadius: '2px',
      },
    },
  })
);

export const ColorPicker: React.FC<StandardEditorProps<any, any>> = props => {
  const [color, setColor] = useState(props?.value);

  const handleChange = (e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
    setColor(e.target.value);
  };

  const setFieldValue = (color: string) => {
    props.onChange(color);
  };

  const classes = useStyles();
  return (
    <ColorSelector
      defaultColor="#000"
      handleChange={color => {
        setColor(color);
        setFieldValue(color);
      }}
      disabled={false}
      visible={false}
    >
      {({ color: colorFromSelector, handleClick, handleChange: handleChangeFromSelector, force }) => {
        return (
          <div>
            <div className={classes.colorPreview} onClick={handleClick}>
              <div style={{ backgroundColor: force ? colorFromSelector : color }} />
            </div>
            <TextField
              variant="outlined"
              size="small"
              label="Color"
              type="text"
              name="color"
              value={color}
              className={classes.textField}
              InputLabelProps={{
                shrink: true,
              }}
              onChange={e => {
                handleChangeFromSelector(e);
                handleChange(e);
              }}
            />
          </div>
        );
      }}
    </ColorSelector>
  );
};
