import * as React from 'react';
import { PanelPlugin } from '@grafana/data';
import { Comparator, Image, PanelType, SimpleOptions } from './types';
import { BasePanel } from './BasePanel';
import { MultiSwitchTab } from './fields/MultiSwitchTab';
import { ColorPicker } from './fields/ColorPicker';

export const plugin = new PanelPlugin<SimpleOptions>(BasePanel).setPanelOptions(builder => {
  return builder
    .addSelect({
      path: 'panelType',
      name: 'Panel Type',
      settings: {
        options: [
          { value: PanelType.Slider, label: PanelType.Slider },
          { value: PanelType.SingleStat, label: PanelType.SingleStat },
          { value: PanelType.Switch, label: PanelType.Switch },
          { value: PanelType.MultiSwitch, label: PanelType.MultiSwitch },
        ],
      },
      defaultValue: PanelType.Slider,
    })
    .addNumberInput({
      path: 'min',
      name: 'Min value',
      defaultValue: 0,
      category: [PanelType.Slider],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.Slider;
      },
    })
    .addNumberInput({
      path: 'max',
      name: 'Max value',
      defaultValue: 100,
      category: [PanelType.Slider],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.Slider;
      },
    })
    .addCustomEditor({
      id: 'multiSwitchTab',
      path: 'multiSwitchTab',
      name: 'Number of Count',
      category: [PanelType.MultiSwitch],
      editor: props => {
        return <MultiSwitchTab {...props} />;
      },

      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.MultiSwitch;
      },
    })
    .addTextInput({
      path: 'unit',
      name: 'Unit',
      defaultValue: '',
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat;
      },
    })
    .addNumberInput({
      path: 'fontSize',
      name: 'Font Size',
      defaultValue: 40,
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat;
      },
    })
    .addCustomEditor({
      id: 'fontColor',
      path: 'fontColor',
      name: 'Font Color',
      category: [PanelType.SingleStat],
      editor: props => {
        return <ColorPicker {...props} />;
      },
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat;
      },
    })
    .addNumberInput({
      path: 'precision',
      name: 'Precision (works only on Numeric values)',
      defaultValue: 0,
      settings: {
        min: 0,
        integer: true,
      },
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat;
      },
    })
    .addSelect({
      path: 'image',
      name: 'Image',
      settings: {
        options: [
          { value: Image.NoImage, label: Image.NoImage },
          { value: Image.Light, label: Image.Light },
          { value: Image.Fan, label: Image.Fan },
          { value: Image.Cooling, label: Image.Cooling },
          { value: Image.Heating, label: Image.Heating },
          { value: Image.Alert, label: Image.Alert },
        ],
      },
      defaultValue: Image.NoImage,
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat;
      },
    })
    .addSelect({
      path: 'comparator',
      name: 'Comparator',
      settings: {
        options: [
          { value: Comparator.Equal, label: Comparator.Equal },
          { value: Comparator.GreaterThan, label: Comparator.GreaterThan },
          { value: Comparator.GreaterThanOrEqual, label: Comparator.GreaterThanOrEqual },
          { value: Comparator.LessThan, label: Comparator.LessThan },
          { value: Comparator.LessThanOrEqual, label: Comparator.LessThanOrEqual },
        ],
      },
      defaultValue: Comparator.Equal,
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat && currentConfig.image !== Image.NoImage;
      },
    })
    .addTextInput({
      path: 'trueState',
      name: 'True State (e.g. xyz, true, 20, ...)',
      defaultValue: '',
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat && currentConfig.image !== Image.NoImage;
      },
    })
    .addBooleanSwitch({
      path: 'showValue',
      name: 'Show Value (on Top)',
      defaultValue: true,
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return currentConfig.panelType === PanelType.SingleStat && currentConfig.image !== Image.NoImage;
      },
    })
    .addTextInput({
      path: 'trueStatus',
      name: 'True Status',
      defaultValue: '',
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return (
          currentConfig.panelType === PanelType.SingleStat &&
          currentConfig.image !== Image.NoImage &&
          !!currentConfig?.showValue
        );
      },
    })
    .addTextInput({
      path: 'falseStatus',
      name: 'False Status',
      defaultValue: '',
      category: [PanelType.SingleStat],
      showIf: (currentConfig: SimpleOptions) => {
        return (
          currentConfig.panelType === PanelType.SingleStat &&
          currentConfig.image !== Image.NoImage &&
          !!currentConfig?.showValue
        );
      },
    })
    .addCustomEditor({
      id: 'trueFontColor',
      path: 'trueFontColor',
      name: 'True Font Color',
      category: [PanelType.SingleStat],
      editor: props => {
        return <ColorPicker {...props} />;
      },
      showIf: (currentConfig: SimpleOptions) => {
        return (
          currentConfig.panelType === PanelType.SingleStat &&
          currentConfig.image !== Image.NoImage &&
          !!currentConfig?.showValue
        );
      },
    });
});
