import * as React from 'react';
import { Comparator, Image } from 'types';
import { createStyles, makeStyles } from '@material-ui/core/styles';
import StatusImage from './StatusImage';
import { useEffect, useState } from 'react';
import { PanelProps } from '../PanelProps';

interface SingleStatProps extends Omit<Omit<PanelProps, '_client'>, 'topicProtocols'> {}

export default function SingleStatPanel(props: SingleStatProps) {
  const { options, dataValue } = props;
  const {
    image,
    unit,
    fontSize,
    fontColor,
    precision,
    comparator,
    trueState,
    showValue,
    trueStatus,
    falseStatus,
    trueFontColor,
  } = options;
  const [value, setValue] = useState('');
  const [state, setState] = useState(false);
  const [showValueState, setShowValueState] = useState(false);
  const classes = useStyles();

  useEffect(() => {
    let convertedValue = dataValue;
    if (!isNaN(dataValue)) {
      convertedValue = Number(dataValue).toFixed(precision);
    }

    if (image !== Image.NoImage) {
      let isTrue = false;
      if (comparator === Comparator.GreaterThan) {
        if (!isNaN(convertedValue) && !isNaN(Number(trueState))) {
          isTrue = Number(convertedValue) > Number(trueState);
        }
      } else if (comparator === Comparator.GreaterThanOrEqual) {
        if (!isNaN(convertedValue) && !isNaN(Number(trueState))) {
          isTrue = Number(convertedValue) >= Number(trueState);
        }
      } else if (comparator === Comparator.LessThan) {
        if (!isNaN(convertedValue) && !isNaN(Number(trueState))) {
          isTrue = Number(convertedValue) < Number(trueState);
        }
      } else if (comparator === Comparator.LessThan) {
        if (!isNaN(convertedValue) && !isNaN(Number(trueState))) {
          isTrue = Number(convertedValue) <= Number(trueState);
        }
      } else {
        isTrue = convertedValue?.toString() === trueState;
      }
      if (isTrue) {
        setValue(trueStatus || convertedValue?.toString());
      } else {
        setValue(falseStatus || convertedValue?.toString());
      }
      setState(isTrue);
      setShowValueState(showValue || false);
    } else {
      setState(false);
      setShowValueState(true);
      setValue(convertedValue?.toString());
    }
  }, [image, dataValue, trueState, trueStatus, falseStatus, precision, comparator, showValue]);

  return (
    <div className={classes.root}>
      {showValueState && (
        <div
          style={{
            fontSize: fontSize,
            fontWeight: 'bold',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            height: image !== Image.NoImage ? `${fontSize}px` : '100%',
            color: state ? trueFontColor ?? fontColor : fontColor,
          }}
        >
          {value} {unit}
        </div>
      )}
      {image !== Image.NoImage && (
        <div style={{ height: showValueState ? `calc(100% - ${fontSize}px` : '100%' }}>
          <StatusImage image={image || Image.NoImage} state={state} />
        </div>
      )}
    </div>
  );
}

const useStyles = makeStyles(() =>
  createStyles({
    root: {
      display: 'flex',
      flexFlow: 'column',
      height: '100%',
      width: '100%',
      alignItems: 'center',
    },
  })
);
